import React from 'react';
import PropTypes from 'prop-types';
// import { formatReactDate } from '../../../../util';

function formatShortDate(dateString) {
    const months = [
            'Jan',
            'Feb',
            'Mar',
            'Apr',
            'May',
            'Jun',
            'Jul',
            'Aug',
            'Sep',
            'Oct',
            'Nov',
            'Dec',
        ],
        date = new Date(dateString),
        month = months[date.getUTCMonth()];

    return (
        <p className="card--event__label--shortdate">
            <span>{date.getUTCDate()}</span> {month}
        </p>
    );
}

function nth(d) {
    // eslint-disable-next-line no-magic-numbers
    if (d > 3 && d < 21) return 'th';
    // eslint-disable-next-line no-magic-numbers
    switch (d % 10) {
        case 1:  return 'st';
        case 2:  return 'nd';
        case 3:  return 'rd';
        default: return 'th';
    }
}

function formatTime(timeString){
    const time = new Date(timeString);
    return (<React.Fragment>{time.getUTCHours() ? time.getUTCHours() : '00'}:{time.getUTCMinutes() ? time.getUTCMinutes() : '00'}</React.Fragment>);
}
  
function formatDate(dateString){
    const days = [
        'Sunday',
        'Monday',
        'Tuesday',
        'Wednesday',
        'Thursday',
        'Friday',
        'Saturday',
    ],
    months = [
        'January',
        'February',
        'March',
        'April',
        'May',
        'June',
        'July',
        'August',
        'September',
        'October',
        'November',
        'December',
    ],
    date = new Date(dateString),
    day = days[date.getUTCDay()],
    daySuffix = nth(date.getUTCDate()),
    month = months[date.getUTCMonth()],
    year = date.getFullYear();

    return (<React.Fragment>{day}, {date.getUTCDate()} <sup>{daySuffix}</sup> {month} {year}</React.Fragment>);
}

function compareDates(startDate, endDate){
    const sDate = new Date(startDate),
        eDate = new Date(endDate),
        noTimeSDate = new Date(sDate.setHours(0,0,0)),
        noTimeEDate = new Date(eDate.setHours(0,0,0));

        const eventDate = noTimeSDate.getTime() === noTimeEDate.getTime() ? (
            <p className='card__type true'>
                <time dateTime={`${sDate.getFullYear()}-${sDate.getUTCMonth()}-${sDate.getUTCDay()}`}>
                    {formatDate(startDate)}, {formatTime(startDate)} - {formatTime(endDate)}
                </time>
            </p>
        ) : (
            <p className='card__type false'>
                <time dateTime={`${sDate.getFullYear()}-${sDate.getUTCMonth()}-${sDate.getUTCDay()}`}>
                    {formatDate(startDate)} - {formatDate(endDate)}
                </time>
            </p>
        );

        return eventDate;
}

/**
 * Render a Funnelback result as an event card.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function Finder__Results__Event(props) {
    const shortDate =
            props.details.metaData.d &&
            formatShortDate(props.details.metaData.d.split('|')[0]),
        location = props.details.metaData.location && (
            <p className="card__tag">
                <span
                    className="fas fa-fw icon fa-map-marker-alt"
                    aria-hidden="true"
                ></span>{' '}
                <span>Location:</span>{' '}
                <span>
                    {props.details.metaData.location.split('|').join(', ')}
                </span>
            </p>
        ),
        audience = props.details.metaData.audience && (
            <p className="card__tag">
                <span
                    className="fas fa-fw icon fa-users"
                    aria-hidden="true"
                ></span>{' '}
                <span>Audience:</span>{' '}
                <span>{props.details.metaData.audience}</span>
            </p>
        ),
        thumbnail = props.details.metaData.thumbnail && (
            <img
                src={props.details.metaData.thumbnail}
                alt=""
                className="card__thumbnail"
            />
        ),
        eventStartDate = props.details.metaData.d &&
            props.details.metaData.d.split('|')[0],
        eventEndDate = props.details.metaData.d && 
            props.details.metaData.d.split('|')[1],
        eventLabel = compareDates(eventStartDate,eventEndDate);

    return (
        <li className="card card--event card--landscape">
            <a
                href={props.details.clickTrackingUrl}
                className="card__anchor card__details"
            >
                {thumbnail}
                {shortDate}
                <div className="card__details__text">
                    <h3 className="card__heading">{props.details.title}</h3>
                    <p className="card__description">
                        {props.details.metaData.c && props.details.metaData.c}
                    </p>
                    {eventLabel}
                    {location}
                    {audience}
                </div>
            </a>
        </li>
    );
}

Finder__Results__Event.propTypes = {
    details: PropTypes.object,
};

export default Finder__Results__Event;
